/* ------------------------------------------------------------------------------------------------ */
/* Header */
/* ------------------------------------------------------------------------------------------------ */

main {
  padding-top: 122px; }

.header {
  background: $primary;
  position: fixed;
  z-index: 100;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  .sub-menu {
    position: absolute;
    top: calc(100% + 18px);
    left: 0;
    background: #FFF;
    width: 180px;
    border-radius: 3px;
    transform: scaleY(0);
    transform-origin: top center;
    opacity: 0;
    visibility: hidden;
    transition: all .3s ease-in-out;
    // box-shadow: 0 2px 4px 0 rgba(23,35,69,0.1)
    box-shadow: 0 10px 50px 0 rgba(0,0,0,.2);
    ul {
      padding: 12px 0;
      &> li {
        display: block; }
      a {
        display: block;
        padding: 6px 16px; } }
    &:before {
      content: "";
      display: block;
      width: 100%;
      height: 30px;
      background: transparent;
      position: absolute;
      top: -18px;
      left: 0; }
    &:after {
      content: "";
      display: block;
      @include triangle(up,18px,9px,#FFFFFF);
      position: absolute;
      top: -9px;
      left: 16px; }
    &.sub-menu--cart {
      width: 280px; }
    .cart {
      &.cart--empty {
        padding: 18px 30px;
        text-align: center;
        p {
          margin-bottom: 16px; } } } }
  &__top {
    background: #FFF;
    max-height: 50px;
    padding: 10px 0;
    opacity: 1;
    display: flex;
    transition: all .3s ease-in-out;
    .inline-contact {
      justify-content: flex-end; } }
  &__main {
    position: relative;
    padding: 15px 0;
    transition: all .3s ease-in-out;
    .container {
      display: flex;
      align-items: center; } }
  &__logo {
    display: block;
    margin-right: auto;
    img {
      display: block;
      height: 50px;
      width: auto; } }
  &__menu {
    display: block;
    &-item {
      display: inline-block;
      padding: 0 15px;
      &.has-sub {
        position: relative;
        .header__menu-link {
          position: relative;
          padding-right: 20px; }
        .header__menu-link:after {
          content: "";
          display: block;
          position: absolute;
          right: 0;
          top: 10px;
          width: 0;
          height: 0;
          border-style: solid;
          border-width: 4px 4px 0 4px;
          border-color: #FFF transparent transparent transparent;
          transition: all .3s ease-in-out; } }
      &.has-sub--lg {
        position: static;
        .sub-menu {
          width: 1260px;
          left: calc(50% - 630px);
          top: auto;
          margin-top: 29px;
          border-radius: 0 0 3px 3px;
          padding: 30px 60px;
          &:after {
            display: none; }
          &:before {
            top: -30px; }
          h2 {
            font-size: 18px;
            line-height: 24px;
            font-weight: 500;
            margin-bottom: 0;
            padding: 6px 0;
            a {
              color: $primary;
              &:hover {
                color: $secondary;
                text-decoration: none; } } }
          ul:not(:first-child) {
            padding-top: 0; }
          ul > li a {
            padding: 6px 0; }
          .header-slider {
            h2 {
              font-weight: 400;
              color: #444;
              margin-bottom: 12px; } } } } }
    &-link {
      color: #FFF;
      transition: all .3s ease-in-out;
      &:hover {
        color: $secondary; } } }
  &__menu-icon {
    display: block;
    position: relative;
    margin-left: 60px;
    &-item {
      display: inline-block;
      padding: 0 15px;
      position: relative;
      .sub-menu {
        left: auto;
        right: 0;
        &:after {
          left: auto;
          right: 16px; } }
      &:last-child {
        padding-right: 0; }
      &:hover {
        .header__menu-icon-link .svg path {
          fill: $secondary; }
        &> .sub-menu {
          transform: scaleY(1);
          opacity: 1;
          visibility: visible; } } }
    &-link {
      color: #FFF;
      position: relative;
      cursor: pointer;
      .svg {
        height: 20px;
        width: auto;
        path {
          fill: #FFF;
          transition: all .3s ease-in-out; } }
      .icon-badge {
        position: a;
        position: absolute;
        right: -10px;
        top: -5px;
        span {
          position: relative;
          z-index: 2;
          display: block;
          height: 16px;
          min-width: 16px;
          padding: 0 4px;
          border-radius: 8px;
          background: $secondary;
          color: #FFF;
          text-align: center;
          font-size: 11px;
          line-height: 16px;
          font-weight: bold; }
        &:before {
          content: "";
          position: absolute;
          top: -2px;
          left: -2px;
          width: calc(100% + 4px);
          height: calc(100% + 4px);
          background: $primary; } }
      &:hover {
        color: $secondary;
        .svg path {
          fill: $secondary; } } } }

  // STICKY HEADER
  &.sticky {
    .header__top {
      max-height: 0;
      padding: 0;
      opacity: 0;
      visibility: hidden; }
    .header__main {
      padding: 5px 0; } } }


@include responsive(0,1200px) {
  .header {
    &__menu {
      display: block;
      &-item {
        &.has-sub {
          &:hover {
            .header__menu-link {
              color: $secondary;
              &:after {
                border-color: $secondary transparent transparent transparent; } }
            &> .sub-menu {
              transform: scaleY(1);
              opacity: 1;
              visibility: visible; } } } } }
    .slider {
      &__item {
        position: relative;
        width: 100%; }
      &__img {
        display: block;
        width: 100%;
        margin-bottom: 12px;
        .img {
          width: 100%;
          padding-top: 110%;
          background: no-repeat center;
          background-size: cover; } }
      &__content {
        display: block;
        text-align: center; }
      &__title {
        font-size: 16px;
        line-height: 24px;
        margin: 0; }
      .owl-dots {
        display: none; } }
    &.sticky {
      .has-sub.has-sub--lg .sub-menu {
        margin-top: 19px; } } } }


@include responsive(1200px) {
  .header {
    .header-toggle {
      width: 30px;
      height: 23px;
      margin: 10px 0;
      margin-left: 24px;
      position: relative;
      z-index: 210;
      top: 0;
      transition: all .5s ease-in-out;
      span {
        display: block;
        height: 3px;
        width: 100%;
        background: #FFF;
        position: absolute;
        left: 0;
        border-radius: 2px;
        opacity: 1;
        transform: none;
        transition: all .3s ease-in-out;
        &:nth-child(1) {
          top: 0; }
        &:nth-child(2) {
          top: 10px; }
        &:nth-child(3) {
          top: 20px; } } }
    &__menu {
      position: fixed;
      width: 100%;
      height: 100%;
      overflow: auto;
      top: 0;
      background: #FFF;
      z-index: 200;
      padding: 120px 48px;
      left: -100%;
      opacity: 0;
      visibility: hidden;
      transition: all .3s ease-in-out;
      // opacity: 1
      // left: 0
      // visibility: visible
      .sub-menu {
        position: relative;
        left: auto;
        top: auto;
        max-height: 0;
        padding-top: 0;
        padding-bottom: 0;
        &:before,
        &:after {
          display: none; } }
      &-link {
        font-size: 20px;
        line-height: 30px; }
      &-item {
        display: block;
        padding: 10px 0;
        &.has-sub {
          .header__menu-link {
            pointer-events: none; }
          .header__menu-link:after {
            top: 13px;
            border-color: $primary transparent transparent transparent; }
          &.has-sub--lg {
            .hide-lg {
              display: none; }
            .sub-menu {
              position: relative;
              padding: 0 16px;
              left: auto;
              margin: 0;
              width: 100%;
              ul > li a {
                padding: 0; } } }
          &.sub-open {
            .sub-menu {
              max-height: 200vh;
              margin: 15px 0 0;
              transform: scaleY(1);
              opacity: 1;
              visibility: visible; }
            &.has-sub--lg .sub-menu {
              padding: 20px 16px;
              ul > li a {
                padding: 6px 0; } } } } }

      &-link {
        color: $primary; } } }

  body.menu-open {
    overflow: hidden;
    .sidebar {
      display: none; }
    .header__menu {
      opacity: 1;
      visibility: visible;
      left: 0; }
    .header-toggle {
      position: fixed;
      top: 30px;
      right: 30px;
      transition: all .5s ease-in-out;
      span {
        background: $primary;
        &:nth-child(1) {
          top: 10px;
          transform: rotate(45deg); }
        &:nth-child(2) {
          top: 10px;
          opacity: 0; }
        &:nth-child(3) {
          top: 10px;
          transform: rotate(135deg); } } } } }


@include mobile {
  .header {
    .inline-contact {
      .inline-contact__icon {
        margin: 0; }
      span:not(.inline-contact__icon) {
        display: none; } }
    &__menu {
      padding: 60px 24px;
      &-item {
        .sub-menu {
          box-shadow: none;
          border-bottom: 1px solid $border;
          border-radius: 0; }
        &.has-sub--lg {
          .sub-menu {
            h2 a {
              font-size: 18px;
              line-height: 26px;
              font-weight: 400;
              color: $text; }
            ul {
              max-height: 0;
              opacity: 0;
              padding: 0;
              visibility: hidden; } } } }
      &-item.has-sub.sub-open {
        .sub-menu {
          margin: 0;
          width: 100%;
          ul {
            a {
              padding: 6px 15px;
              font-size: 18px;
              line-height: 26px;
              font-weight: 400;
              color: $text; } } }
        &.has-sub--lg {
          .sub-menu {
            padding: 12px 10px; } } } }

    &__menu-icon {
      margin-left: 20px;
      &-item {
        padding: 0 8px; } } }
  body.menu-open {
    .header-toggle {
      top: 24px;
      right: 16px; } } }

