/* ------------------------------------------------------------------------------------------------ */
/* Custom Plugins */
/* ------------------------------------------------------------------------------------------------ */

// boostrap select

.dropdown-menu {
  border-radius: 0;
  border: 0;
  box-shadow: 0 10px 50px 0 rgba(0,0,0,.2);
  padding: 0.6875rem 0; }

.dropdown-item {
  padding: 0.5rem 0.875rem;
  color: $primary;
  transition: all .5 ease-in-out;
  &:not([href]) {
    color: $primary;
    &:hover {
      background: $light;
      color: $primary; }
    &:focus {
      outline: none; }
    &:active,
    &.active {
      background: transparent;
      font-weight: 600; } } }

.dropdown-toggle::after,
.dropup .dropdown-toggle::after {
  display: block;
  content: "";
  border: 0;
  width: 20px;
  height: 20px;
  margin: 0;
  position: absolute;
  right: 0.875rem;
  top: calc(50% - 10px);
  background: url(../images/chevron-down.svg) no-repeat center;
  background-size: contain; }
.dropup .dropdown-toggle::after {
  transform: scaleY(-1); }

.bootstrap-select .dropdown-toggle:focus,
.bootstrap-select > select.mobile-device:focus + .dropdown-toggle {
  outline: none !important; }

.bootstrap-select {
  .dropdown-menu {
    z-index: 1060; }
  .btn {
    padding: 8px 16px;
    border: 1px solid $text;
    background: #FFF;
    position: relative;
    z-index: 6;
    transition: all .3s ease-in-out; }
  &.show {
    .btn {
      border-color: $primary;
 } } }      // box-shadow: 0px 0px 0px 1px $primary


// OWL CAROUSEL
.owl {
  &-carousel {
    position: relative; }
  &-nav {
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;
    height: 0;
    .disabled {
      opacity: .5; }
    button:focus {
      outline: none !important; }
    .btn-nav {
      width: 30px;
      height: 60px;
      background-color: $primary;
      position: absolute;
      top: -30px;
      background-size: contain;
      transition: all .3s ease-in-out;
      cursor: pointer;
      &:hover {
        opacity: 1; }
      &.left {
        left: 0;
        background: $primary url(../images/icon-chevron-left.svg) no-repeat center; }
      &.right {
        right: 0;
        background: $primary url(../images/icon-chevron-right.svg) no-repeat center; } } }
  &-dots {
    text-align: center;
    position: absolute;
    bottom: 0;
    margin: 0;
    width: 100%;
    line-height: 0;
    height: 0; }
  &-dot {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    margin: 0 5px;
    width: auto;
    span {
      position: relative;
      top: -6px;
      display: block;
      width: 30px;
      height: 6px;
      border-radius: 2px;
      background-color: #e9e9e9;
      opacity: .9;
      transition: all .3s ease-in-out; }
    &:focus {
      outline: none !important; }
    &.active span {
      background-color: $primary; } } }


@include mobile {
  .slider-xs {
    position: relative;
    padding-bottom: 24px;
    margin-left: 0;
    margin-right: 0;
    [class*="col-"] {
      padding-left: 0;
      padding-right: 0; } } }


// BOOSTRAP DATEPICKER
.datepicker-dropdown:after,
.datepicker-dropdown:before {
  display: none; }
.datepicker-orient-top {
  margin-top: 22px; }
.datepicker-dropdown {
  padding: 1.5rem 2rem; }
.datepicker {
  .next,
  .prev {
    font-size: 0;
    line-height: 0;
    color: transparent;
    &:after {
      content: "";
      display: block;
      width: 1rem;
      height: 1.125rem;
      background: url(../images/chevron.svg) no-repeat center;
      background-size: contain;
      margin: 0 auto; } }
  .prev:after {
    transform: scaleX(-1); }
  .datepicker-switch,
  .next,
  .prev {
    padding-bottom: 1rem;
    opacity: 1;
    transition: all .3s ease-in-out;
    &:hover {
      background: transparent !important;
      opacity: .7; } }
  table tr {
    td, th {
      border-radius: 0;
      width: 3.25rem;
      height: 2.125rem;
      font-size: 0.875rem;
      line-height: 2em;
      font-weight: 400;
      color: $primary; }
    th.datepicker-switch {
      text-transform: uppercase;
      font-size: 1.5rem;
      line-height: 1.25em;
      font-weight: 700; }
    th.dow {
      text-transform: uppercase; }
    td.day {
      background: #FFF;
      &:hover {
        background: transparent; }
      &.disabled:not(.old):not(.new) {
        color: $text !important;
        opacity: 0.6; }
      &.old,
      &.new {
        height: 0;
        max-height: 0;
        font-size: 0;
        line-height: 0;
        opacity: 0; }
      &.active.active,
      &.active.disabled,
      &.active.disabled.active,
      &.active.disabled.disabled,
      &.active.disabled:active,
      &.active.disabled:hover,
      &.active.disabled:hover.active,
      &.active.disabled:hover.disabled,
      &.active.disabled:hover:active,
      &.active.disabled:hover:hover,
      &.active.disabled:hover[disabled],
      &.active.disabled[disabled],
      &.active:active,
      &.active:hover,
      &.active:hover.active,
      &.active:hover.disabled,
      &.active:hover:active,
      &.active:hover:hover,
      &.active:hover[disabled],
      &.active[disabled] {
        background: transparent;
        position: relative;
        color: #FFF;
        font-weight: 700;
        &:before {
          content: "";
          display: block;
          width: 2rem;
          height: 2rem;
          background-color: $primary;
          position: absolute;
          top: calc(50% - 1rem);
          left: calc(50% - 1rem);
          z-index: -1; } } } } }


.datepicker table tr td span.active,
.datepicker table tr td span.active.disabled,
.datepicker table tr td span.active.disabled:hover,
.datepicker table tr td span.active:hover {
  background: $primary !important; }

@include maxtablet {

  .datepicker-backdrop,
  .select-backdrop {
    position: fixed;
    z-index: 1050;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.6); }

  .datepicker-dropdown {
    padding: 1rem .5rem; }
  .datepicker {
    table tr {
      td, th {
        border-radius: 0;
        width: 3rem; } } } }



// LSB
.lsb-control-panel {
  display: none; }
.lightspeed-box .lsb-header {
  font-size: 13px;
  font-weight: 40;
  top: 20px;
  left: 40px; }
.lsb-control.lsb-close {
  svg {
    display: none; }
  &:after {
    content: "";
    display: block;
    width: 1.375rem;
    height: 1.375rem;
    position: absolute;
    top: 20px;
    right: 30px;
    background: url(../images/ic-close.svg) no-repeat center;
    background-size: contain; } }
