/* ------------------------------------------------------------------------------------------------ */
/* Responsive */
/* ------------------------------------------------------------------------------------------------ */

@include responsive(1200px) {
  .masthead {
    &__item {
      height: 50vh;
      min-height: 400px; } } }


@include maxtablet {
  .sidebar {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: -100%;
    background: #FFF;
    z-index: 150;
    padding: 0 !important;
    transition: all .3s ease-in-out;
    &__close {
      display: block;
      position: absolute;
      z-index: 5;
      top: 30px;
      right: 20px;
      .svg {
        width: 24px;
        height: 24px;
        line {
          stroke: $primary; } } }
    &__toggle {
      display: block;
      padding: 5px 16px 3px;
      width: auto;
      background: $primary;
      position: absolute;
      white-space: nowrap;
      font-size: 12px;
      line-height: 16px;
      text-transform: uppercase;
      letter-spacing: 1.5px;
      font-weight: 500;
      top: 200px;
      left: 100%;
      color: #FFF;
      text-align: center;
      transform: rotate(90deg);
      transform-origin: bottom left; }
    &__wrap {
      max-height: 100vh;
      position: relative;
      overflow: auto;
      padding: 100px 60px; } }
  body.sidebar-open {
    .sidebar {
      left: 0; } }

  .home-products {
    .product .col-lg-6 {
      margin-bottom: 30px; } }

  .about-item {
    margin-bottom: 48px;
    &.about-item--right {
      text-align: left;
      padding-left: 0;
      padding-left: 75px;
      padding-right: 0;
      .img {
        left: 0;
        right: auto; } } }

  .about-block {
    margin-top: 0;
    margin-bottom: 48px; }


  .product-single__content {
    padding: 48px 0; } }


@include mobile {
  .page {
    padding-left: 16px;
    padding-right: 16px;
    padding-bottom: 60px; }
  .page-head {
    h1 {
      padding-left: 16px; } }

  .section-title {
    display: block; }
  section.block {
    margin-top: 30px;
    margin-bottom: 30px; }

  .masthead {
    &__item {
      height: 100vh;
      max-height: auto; }
    &__content {
      span {
        font-size: 20px;
        line-height: 30px; }
      h1 {
        font-size: 48px;
        line-height: 56px; } } }

  .home-products {
    .product__item {
      margin-bottom: 0; } }

  .home-expertise {
    .expertise__bg {
      padding-top: 50%; }
    .owl-nav {
      width: auto;
      margin: 0 -15px; } }

  .video-block {
    &__content {
      padding-top: 30px; } }

  .home-news {
    .news__item {
      margin-bottom: 0; }
    .owl-nav {
      display: none; } }


  .product {
    &__badge {
      top: 0;
      left: 0;
      &:before {
        display: none; } } }


  .product-toolbar {
    .form-group label {
      display: none; } }

  .team {
    margin: 0; }

  .our-work {
    width: auto;
    margin-left: -16px;
    margin-right: -16px;
    &__title {
      .title {
        font-size: 28px; }
      p {
        max-width: 100%; } }
    .owl-nav {
      display: none; } }


  .page--contact {
    .address {
      margin-top: 48px; } } }
