/* ------------------------------------------------------------------------------------------------ */
/* Footer */
/* ------------------------------------------------------------------------------------------------ */

.footer {
  position: relative;
  background: #f7f7f7;
  border-top: 1px solid $border;
  &__top {
    padding-top: 32px;
    padding-bottom: 32px;
    .social-media {
      font-size: 24px;
      line-height: 30px;
      text-align: right; } }
  &__logo {
    display: inline-block;
    img {
      display: block;
      height: 50px;
      width: auto; } }
  &__main {
    padding-bottom: 15px;
    .footer-menu,
    .footer-contact {
      display: block;
      position: relative;
      width: 100%;
      margin-bottom: 30px;
      .icon {
        margin-right: 12px; }
      &__title {
        font-size: 20px;
        line-height: 30px;
        font-weight: 500;
        color: $text;
        margin-bottom: 16px; }
      &__wrap {
        display: block;
        width: 100%;
        position: relative; }
      &__item {
        display: block;
        margin-bottom: 8px;
        &:last-child {
          margin-bottom: 0; } } }
    .footer-contact {
      p {
        margin-bottom: 16px; }
      ul > li {
        display: block;
        margin-bottom: 8px;
        &:last-child {
          margin-bottom: 0; } } } }

  &__menu-bot {
    text-align: center;
    padding: 20px 0;
    border-top: 1px solid $border;
    .menu-bot {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      &__item {
        display: block;
        position: relative;
        padding: 0 15px;
        margin: 4px 0;
        &:not(:first-child):before {
          content: "";
          display: block;
          height: 100%;
          width: 1px;
          background: #777;
          position: absolute;
          top: 0;
          left: 0; } } } }
  &__copyright {
    display: inline-block;
    position: relative;
    font-size: 14px; }
  &__bot {
    background: #efefef;
    padding: 20px 0;
    .payment {
      justify-content: flex-end; } } }

@include maxtablet {
  .footer {
    &__top {
      text-align: center;
      .social-media {
        text-align: center; } }
    &__menu-bot {
      .menu-bot__item:not(:first-child):before {
        display: none; }
      .menu-bot__item {
        padding: 0 12px; } }
    &__copyright {
      text-align: center;
      width: 100%;
      display: block; }
    &__bot {
      .payment {
        justify-content: center;
        padding-top: 16px; } } } }
