/* ------------------------------------------------------------------------------------------------ */
/* General */
/* ------------------------------------------------------------------------------------------------ */

.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto; }

@media (min-width: 1200px) {
  .container {
    max-width: 1260px; } }


// Component
.btn {
  font-size: 1rem;
  line-height: 22px;
  border-radius: 4px;
  padding: 8px 16px;
  font-weight: 500;
  transition: all .5s ease-in-out;
  &:hover {
    color: $text;
    text-decoration: none; }
  &:focus, &.focus {
    outline: 0;
    box-shadow: none; }
  &.disabled,
  &:disabled {
    opacity: 0.5; }

  .btn-icon {
    position: relative;
    top: -1px;
    display: inline-block;
    vertical-align: middle;
    height: 16px;
    line-height: 22px;
    width: auto;
    margin-left: 12px; }

  &-primary {
    color: #FFF;
    background-color: $primary;
    border-color: $primary; }
  &-primary:hover {
    color: #FFF;
    background-color: darken($primary, 10%);
    border-color: darken($primary, 10%); }
  &-primary:focus, &-primary.focus {
    box-shadow: none; }
  &-primary.disabled, &-primary:disabled {
    color: #FFF;
    background-color: $primary;
    border-color: $primary; }
  &-primary:not(:disabled):not(.disabled):active,
  &-primary:not(:disabled):not(.disabled).active,
  .show > &-primary.dropdown-toggle {
    color: #fff;
    background-color: darken($primary, 10%);
    border-color: darken($primary, 10%);
    box-shadow: none; }

  &-secondary {
    color: #FFF;
    background-color: $secondary;
    border-color: $secondary; }
  &-secondary:hover {
    color: #FFF;
    background-color: $primary;
    border-color: $primary; }
  &-secondary:focus, &-secondary.focus {
    box-shadow: none; }
  &-secondary.disabled, &-secondary:disabled {
    color: #FFF;
    background-color: $secondary;
    border-color: $secondary; }
  &-secondary:not(:disabled):not(.disabled):active,
  &-secondary:not(:disabled):not(.disabled).active,
  .show > &-secondary.dropdown-toggle {
    color: #fff;
    background-color: darken($secondary, 10%);
    border-color: darken($secondary, 10%); }
  &-white {
    color: $primary;
    background-color: #FFF;
    border-color: #FFF; }
  &-white:hover {
    color: #FFF;
    background-color: $primary;
    border-color: $primary; }
  &-white:focus, &-white.focus {
    box-shadow: none; }
  &-white.disabled, &-white:disabled {
    color: #FFF;
    background-color: #FFF;
    border-color: #FFF; }
  &-white:not(:disabled):not(.disabled):active,
  &-white:not(:disabled):not(.disabled).active,
  .show > &-white.dropdown-toggle {
    color: #fff;
    background-color: #DDD;
    border-color: #DDD; }

  &-link {
    color: $text;
    &:hover {
      color: $secondary; } } }

.btn-more {
  color: $secondary;
  &:after {
    content: "";
    display: inline-block;
    top: 2px;
    margin-left: 6px;
    position: relative;
    width: 0;
    height: 14px;
    background: url(../images/arrow.svg) no-repeat center;
    background-size: contain;
    left: -16px;
    opacity: 0;
    transition: all .3s ease-in-out; }
  &:hover {
    &:after {
      width: 16px;
      left: 0;
      opacity: 1; } } }

.breadchrumb {
  padding: 15px 0;
  margin-bottom: 20px;
  &__item {
    position: relative;
    display: inline-block;
    &:not(:first-child):before {
      content: "/";
      display: inline-block;
      margin: 0 16px; } } }

.page-head {
  padding: 0 0 30px;
  .page-title {
    font-size: 48px;
    line-height: 60px;
    &:first-child {
      padding-top: 60px; } } }


.page {
  padding: 0 0 120px; }

section.block {
  margin-top: 40px;
  padding-bottom: 40px; }
.pt-4 {
  padding-top: 40px; }
.pb-4 {
  padding-bottom: 40px; }
.mt-4 {
  margin-top: 40px; }
.mb-4 {
  margin-bottom: 40px; }

.section-title {
  margin-bottom: 24px;
  display: flex;
  align-items: flex-end;
  .title {
    display: block;
    font-size: 28px;
    line-height: 40px;
    font-weight: 500;
    color: $primary;
    margin: 0; }
  .btn-more {
    display: block;
    font-size: 15px;
    line-height: 31px;
    font-weight: 400;
    margin-left: auto; } }

.social-media {
  display: block;
  margin: 0 -5px;
  &__item {
    display: inline-block;
    padding: 0 5px; }
  &__link {
    color: #434343;
    &:hover {
      color: $secondary; } } }

.inline-contact {
  display: flex;
  margin: 0 -12px;
  &__item {
    display: block;
    position: relative;
    padding: 0 12px;
    color: #434343;
    &:not(:first-child):before {
      content: "";
      display: block;
      width: 1px;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background: #ddd; } }
  &__icon {
    display: inline-block;
    margin-right: 8px; }
  &__link {
    display: inline-block;
    color: #434343;
    &:hover {
      color: $secondary; } } }

.payment {
  display: flex;
  flex-wrap: wrap;
  margin-left: -6px;
  margin-right: -6px;
  &__item {
    display: block;
    padding: 0 6px;
    opacity: 0.6;
    transition: all .3s ease-in-out;
    &:hover {
      opacity: 1; } } }




.instagram-block {
  display: flex;
  flex-wrap: wrap;
  &__item {
    position: relative;
    width: 100%;
    max-width: 33.333333%;
    flex: 0 0 33.333333%;
    padding-right: 1px;
    padding-bottom: 1px;
    .img {
      width: 100%;
      padding-top: 100%;
      background: no-repeat center;
      background-size: cover; } } }


.pagination {
  border-radius: 0 !important; }
.page-item {
  display: block;
  margin: 0 1px;
  border-radius: 0 !important;
  &.active .page-link {
    color: #FFF;
    background-color: $secondary; }
  &:first-child .page-link,
  &:last-child .page-link {
    border-radius: 0;
    color: $primary;
    &:hover {
      background-color: transparent;
      color: $primary; } }
  span,
  .page-link {
    position: relative;
    display: block;
    padding: 0 .5rem;
    min-width: 1.625rem;
    margin: 0;
    font-size: 1rem;
    line-height:  1.625em;
    text-align: center;
    color: $text;
    background-color: transparent;
    border: 0;
    border-radius: 4px;
    transition: all .3s ease-in-out;
    &:hover {
      color: #FFF;
      background-color: $secondary; } }
  span:hover {
    pointer-events: none;
    background-color: transparent;
    color: $text; } }



.modal-search {
  z-index: 150;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  visibility: hidden;
  transition: all .3s ease-in-out;
  &:before {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 0 0 0 100%;
    transform: scale(0.1);
    transform-origin: top right;
    background: rgba(0,0,0,.9);
    transition: all 0.7s ease-in-out; }
  &__wrap {
    width: 100%;
    max-width: 70%;
    margin: 30px auto; }
  .close {
    position: absolute;
    top: 30px;
    right: 30px;
    cursor: pointer;
    opacity: 0;
    transition: all .3s ease-in-out;
    .svg {
      width: 32px;
      height: 32px; }
    &:hover {
      opacity: .5; } } }

.form-search {
  position: relative;
  opacity: 0;
  left: 100%;
  position: relative;
  border-bottom: 1px solid #fff;
  transition: all .3s ease-in-out;
  transition-delay: 0.7s;
  input {
    display: block;
    width: 100%;
    height: 60px;
    background: transparent;
    border: 0;
    padding: 6px 0;
    font-size: 20px;
    color: #FFF;
    &:focus {
      outline: none; }
    &::placeholder {
      color: rgba(255,255,255,.9); } }
  .btn {
    position: absolute;
    right: 0;
    top: 0;
    padding: 10px 0;
    min-width: 30px;
    .svg {
      height: 40px;
      width: auto; } } }

body.search-open {
  overflow: hidden;
  .modal-search {
    opacity: 1;
    visibility: visible;
    &:before {
      transform: scale(2); }
    .close {
      opacity: 1; } }
  .form-search {
    left: 0;
    opacity: 1; } }


.accordion {
  [data-toggle] {
    cursor: pointer; }
  .title {
    font-size: 20px;
    line-height: 30px;
    font-weight: 500;
    color: #222;
    margin-bottom: 10px;
    &:last-child {
      margin-bottom: 0; } }
  .card {
    border-radius: 0;
    background: #FFF;
    border-width: 1px 0;
    border-style: solid;
    border-color: $border;
    &-header {
      position: relative;
      background: #FFF;
      border: 0;
      padding: 20px 0 20px 30px;
      &-icon {
        width: 14px;
        height: 14px;
        display: block;
        position: absolute;
        top: 28px;
        left: 0;
        span {
          display: block;
          width: 14px;
          height: 2px;
          background: $text;
          position: absolute;
          top: calc(50% - 1px);
          left: 0;
          transform: none;
          transition: all .3s ease-in-out; } }
      &.collapsed {
        .card-header-icon span {
          &:nth-child(1) {
            transform: rotate(180deg); }
          &:nth-child(2) {
            transform: rotate(90deg); } } } }
    &-body {
      padding: 10px 0 40px 30px; } }

  article {
    h3 {
      font-size: 16px;
      line-height: 24px;
      font-weight: 600;
      color: #222; } } }


.rate {
  display: flex;
  .count {
    display: block;
    margin: 0 12px;
    font-size: 14px;
    color: #777;
    position: relative;
    top: 1px; }
  &__wrap {
    span {
      display: inline-block;
      font-family: "Font Awesome 5 Free";
      font-weight: 400;
      font-style: normal;
      font-variant: normal;
      text-rendering: auto;
      line-height: 1;
      color: #888;
      &:before {
        content: "\f005"; }
      &.rated {
        font-weight: 600;
        color: $secondary; } } } }

.size-control {
  .size__item {
    display: inline-block;
    &:not(:first-child) {
      margin-left: 6px; }
    label {
      display: block;
      position: relative;
      margin: 0;
      input[type="radio"] {
        position: absolute;
        visibility: hidden;
        opacity: 0; }
      span {
        cursor: pointer;
        display: block;
        position: relative;
        padding: 0 6px;
        min-width: 30px;
        text-align: center;
        font-size: 16px;
        line-height: 26px;
        font-weight: 700;
        border: 2px solid $border; }
      input[type="radio"]:checked + span {
        border-color: $secondary; } } } }

.color-control {
  .color__item {
    display: inline-block;
    &:not(:first-child) {
      margin-left: 6px; }
    label {
      display: block;
      position: relative;
      margin: 0;
      input[type="radio"] {
        position: absolute;
        visibility: hidden;
        opacity: 0; }
      span {
        cursor: pointer;
        display: block;
        position: relative;
        width: 30px;
        line-height: 26px;
        border: 2px solid $border;
        &:before {
          content: "-";
          color: transparent; }
        &:after {
          content: "";
          display: block;
          width: 100%;
          height: 100%;
          position: absolute;
          top: 0;
          left: 0;
          border: 3px solid #FFF; } }
      input[type="radio"]:checked + span {
        border-color: $secondary; } } } }


.quantity-control {
  display: block;
  position: relative;
  max-width: 80px;
  &_min,
  &_plus {
    position: absolute;
    z-index: 4;
    top: 0;
    display: block;
    width: 30px;
    height: 40px;
    background: no-repeat center;
    background-size: 10px;
    cursor: pointer;
    opacity: 1;
    transition: all .3s ease-in-out;
    &:hover {
      opacity: .5; } }
  &_min {
    background-image: url(../images/minus.svg);
    left: 0; }
  &_plus {
    background-image: url(../images/plus.svg);
    right: 0; }
  .form-control {
    text-align: center;
    padding-left: 30px;
    padding-right: 30px; } }


.nav-tabs {
  .nav-link {
    padding: 5px 10px; } }
