/* ------------------------------------------------------------------------------------------------ */
/* Content */
/* ------------------------------------------------------------------------------------------------ */

.masthead {
  margin-top: -122px;
  &__item {
    height: 100vh;
    max-height: 900px;
    width: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center; }
  &__content {
    z-index: 2;
    position: relative;
    width: 100%;
    span {
      font-size: 32px;
      line-height: 48px;
      color: $text; }
    h1 {
      font-size: 64px;
      line-height: 80px;
      font-weight: 500;
      position: relative;
      left: -3px; } }
  &__bg {
    z-index: 1;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: no-repeat center;
    background-size: cover; }
  .owl-dots {
    bottom: 0;
    padding: 24px; } }


// PRODUCT BANNER
.banner {
  position: relative;
  border-radius: 3px;
  overflow: hidden;
  &__content {
    z-index: 2;
    position: absolute;
    width: 100%;
    bottom: 0;
    padding: 30px;
    background: rgba(32, 36, 71, .9);
    color: #FFF;
    h3 {
      color: #FFFc; }
    span {
      display: block;
      color: #FFF; }
    &.banner--yellow {
      background: rgba(250, 185, 21, .9);
      h3, span {
        color: $primary; } } }
  &__img {
    z-index: 1;
    position: relative;
    width: 100%;
    padding-top: 110%;
    background: no-repeat center;
    background-size: cover; } }


// SIDEBAR
.sidebar {
  &__toggle,
  &__close {
    display: none; }
  &__title {
    color: $primary;
    font-size: 18px;
    line-height: 26px;
    font-weight: 500;
    margin-bottom: 12px; }
  &__list {
    margin-bottom: 48px;
    ul {
      &> li {
        display: block;
        border-bottom: 1px dashed $border;
        a {
          display: block;
          padding: 10px 0; } } } }
  &__more {
    display: block;
    padding: 10px 0;
    font-size: 12px;
    line-height: 20px;
    text-align: right;
    cursor: pointer;
    color: $secondary; }

  .badge-color {
    display: inline-block;
    vertical-align: middle;
    position: relative;
    top: -2px;
    width: 16px;
    height: 16px;
    border-radius: 8px;
    line-height: 16px;
    margin: 0 12px 0 0; }

  .badge-size {
    display: inline-block;
    vertical-align: middle;
    position: relative;
    top: -1px;
    min-width: 30px;
    height: 24px;
    text-align: center;
    font-size: 16px;
    line-height: 24px;
    font-weight: 700;
    border: 1px solid $border;
    margin-right: 12px;
    padding: 0 6px; }

  .form-sort-by-price {
    .form-group {
      margin-bottom: 10px;
      position: relative;
      .add-before {
        position: absolute;
        display: block;
        line-height: 40px;
        left: 0;
        top: 0;
        padding: 0 15px; }
      .form-control {
        padding-left: 40px;
        text-align: right; } } }

  &.sidebar--category {
    padding-right: 50px; }
  &.sidebar--post {
    padding-left: 50px; }
  &__item {
    margin-bottom: 48px;
    .product__item {
      margin-bottom: 15px; } } }


// PRODUCT TOOLBAR
.product-toolbar {
  padding: 0 0 48px;
  &__grid {
    a {
      display: inline-block;
      font-weight: normal;
      padding: 9px 13px;
      background: #ddd;
      border-radius: 4px;
      color: $primary;
      &+ a {
        margin-left: 10px; }
      &:hover {
        background: $secondary;
        color: $primary; }
      &.active {
        background: $secondary; } } }
  .form-group {
    display: flex;
    align-items: center;
    label {
      flex-grow: 1;
      flex-basis: 1;
      white-space: nowrap;
      display: block;
      margin-right: 15px;
      text-align: right; }
    .input {
      width: 100%;
      flex-grow: 2;
      flex-basis: 1; }
    &.show {
      .input {
        max-width: 90px; } } }
  .bootstrap-select {
    .btn {
      min-width: 0; }
    &.show {
      .btn {
        background: $secondary;
        border-color: $secondary; } } }
  .page-info {
    padding-top: 30px; } }

// PRODUCT
.product {
  &__item {
    display: block;
    position: relative;
    margin-bottom: 33px;
    &:hover {
      .product__img {
        .img:nth-child(1) {
          left: -100%; }
        .img:nth-child(2) {
          left: 0; } }
      .product__content {
        background: $primary; }
      .product__title {
        color: $secondary; }
      .product__price {
        color: #FFF; } } }
  &__badge {
    position: absolute;
    top: -8px;
    left: -4px;
    z-index: 3;
    padding: 4px 16px;
    background: $primary;
    color: #FFF;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 500;
    letter-spacing: 2px;
    &:before {
      content: "";
      display: block;
      border-style: solid;
      height: 0;
      width: 0;
      border-color: transparent #003E3B transparent transparent;
      border-width: 0 4px 4px 0;
      position: absolute;
      top: 100%;
      left: 0; } }
  &__img {
    display: block;
    position: relative;
    width: 100%;
    padding-top: 110%;
    border-radius: 3px;
    overflow: hidden;
    .img {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background: no-repeat center;
      background-size: cover;
      transition: all .3s ease-in-out;
      &:nth-child(2) {
        left: 100%; } } }
  &__content {
    display: block;
    position: absolute;
    width: 100%;
    left: 0;
    bottom: 0;
    padding: 10px 12px;
    background: $secondary;
    display: flex;
    border-radius: 0 0 3px 3px;
    transition: all .3s ease-in-out; }
  &__title {
    width: 100%;
    max-width: 70%;
    flex: 0 0 70%;
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
    margin: 0;
    color: #222;
    transition: all .3s ease-in-out; }
  &__price {
    width: 100%;
    max-width: 30%;
    flex: 0 0 30%;
    font-weight: 700;
    text-align: right;
    color: #222;
    transition: all .3s ease-in-out;
    .strike {
      display: block;
      font-weight: 400;
      font-size: 13px;
      color: $text-light;
      text-decoration: line-through; } }
  &__pagination {
    padding: 48px 0 0;
    .pagination {
      justify-content: center; } } }


// EXPERTISE
.expertise {
  position: relative;
  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding-top: 18%;
    background: no-repeat center;
    background-size: cover;
    &:after {
      content: "";
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background: rgba(0,0,0,0.8); } }
  &__item {
    margin-top: 120px; }
  &__img {
    width: 60%;
    border-radius: 50%;
    margin: 0 auto 24px;
    position: relative;
    background: #FFF;
    padding: 6px;
    .img {
      position: relative;
      width: 100%;
      padding-top: 100%;
      border-radius: 50%;
      background: no-repeat center;
      background-size: cover; } }

  &__content {
    text-align: center;
    padding: 0 15px; } }


// VIDEO
.video-block {
  position: relative;
  &__wrap {
    position: relative;
    padding: 60px 0; }
  .container {
    z-index: 2;
    position: relative; }
  &__content {
    padding-left: 30px;
    .text {
      font-size: 24px;
      line-height: 36px;
      color: #FFF;
      display: block;
      margin-bottom: 0; }
    .title {
      font-size: 48px;
      line-height: 60px;
      font-weight: 500;
      color: $secondary;
      margin-bottom: 36px; } }
  &__bg {
    z-index: 1;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: no-repeat center;
    background-size: cover;
    &:after {
      content: "";
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background: rgba(0,0,0,0.8); } }
  &__button {
    position: relative;
    display: block;
    border-radius: 3px;
    width: 100%;
    padding-top: 56.2%;
    background: red no-repeat center;
    background-size: cover;
    .svg {
      width: 64px;
      height: 64px;
      position: absolute;
      top: calc(50% - 32px);
      left: calc(50% - 32px);
      transform: scale(1);
      transition: all .3s ease-in-out; }
    &:hover {
      .svg {
        transform: scale(1.1); } } } }


// NEWS
.news {
  &__item {
    display: block;
    position: relative;
    margin-bottom: 48px; }
  &__img {
    position: relative;
    display: block;
    width: 100%;
    margin-bottom: 16px;
    border-radius: 3px;
    overflow: hidden;
    .img {
      width: 100%;
      padding-top: 56%;
      background: no-repeat center;
      background-size: cover;
      transform: scale(1);
      transition: all 1s; }
    &:hover {
      .img {
        transform: scale(1.05); } } }
  &__content {
    .title {
      font-size: 20px;
      line-height: 30px;
      font-weight: 500;
      margin-bottom: 8px;
      a {
        color: $primary;
        &:hover {
          color: $secondary;
          text-decoration: none; } } } }
  &__meta {
    margin-bottom: 16px;
    span {
      display: inline-block;
      margin-right: 16px; } } }


// CLIENTS
.clients {
  &__item {
    display: block;
    padding: 10px;
    border-radius: 3px;
    border: 1px solid $border;
    img {
      display: block;
      margin: 0 auto;
      height: 80px;
      width: auto !important; } }
  .slider-clients {
    margin-left: -10px;
    margin-right: -10px;
    width: auto; }
  .slider__item {
    padding: 0 10px; } }


// CONTACT
.address {
  &__title {
    font-size: 18px;
    line-height: 24px;
    font-weight: 500;
    margin-bottom: 12px; }
  &__item {
    margin-bottom: 32px; }
  .icon {
    margin-right: 12px; }
  p {
    margin-bottom: 16px; }
  ul {
    li {
      display: block;
      margin-bottom: 4px; } } }

.form-contact {
  .form-group {
    position: relative;
    margin-bottom: 30px; }
  .form-group.field-lg {
    border-bottom: 1px solid $border;
    label {
      font-size: 20px;
      line-height: 32px;
      font-weight: 400;
      opacity: .5;
      margin: 0;
      position: absolute;
      top: 14px;
      left: 0;
      z-index: 1;
      transition: all .5s ease-in-out;
      &.label-small {
        font-size: 14px;
        line-height: 20px;
        opacity: 1;
        font-weight: 500;
        color: $primary;
        top: -4px; } } }
  .form-control {
    position: relative;
    z-index: 2;
    background-color: transparent;
    height: 60px;
    border: 0;
    font-size: 16px;
    line-height: 32px;
    padding: 14px 0; }

  .form-action {
    padding-top: 20px;
    text-align: left;
    .btn {
      min-width: 160px;
      padding-top: 12px;
      padding-bottom: 12px; } }

  textarea.form-control {
    height: auto;
    min-height: 60px;
    resize: none; } }

.map {
  position: relative;
  width: 100%;
  overflow-x: hidden;
  &_wrap {
    position: relative; }
  iframe {
    width: 100%;
    height: 60vh;
    display: block; } }



// NEWSLETTER
.newsletter {
  background: $secondary;
  padding: 32px 0;
  &__title {
    font-size: 28px;
    line-height: 40px;
    font-weight: 500;
    color: $primary;
    margin-bottom: 0; }
  &__content {
    color: #FFF; }
  &__form {
    .form-group {
      padding: 0;
      margin: 0;
      display: flex; }
    .form-control {
      border-color: #FFF;
      border-radius: 4px 0 0 4px; }
    .btn {
      border-radius: 0 4px 4px 0; } } }



// CUSTOM EVERY PAGES
.masthead + section.home-products {
  margin-top: 80px; }
section.home-products {
  padding-bottom: 7px; }
section.home-clients {
  padding-bottom: 80px; }


// ABOUT
.page--about {
  h2 {
    font-size: 28px;
    line-height: 40px;
    margin-bottom: 12px; }
  .clients {
    padding-top: 80px;
    .title {
      text-align: center;
      display: block;
      width: 100%; } } }

.about-item {
  padding-left: 75px;
  margin-bottom: 30px;
  position: relative;
  .img {
    position: absolute;
    top: 0;
    left: 0;
    width: 60px;
    height: auto; }
  h2 {
    color: $secondary;
    margin-bottom: 0; }
  hr {
    margin: 12px 0;
    border-top: 1px dashed #434343; }
  h3 {
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 10px; }
  &.about-item--right {
    text-align: right;
    padding-left: 0;
    padding-right: 75px;
    .img {
      left: auto;
      right: 0; } } }

.about-block {
  margin-top: 52px;
  margin-bottom: 30px;
  padding: 36px 30px;
  background: #f7f7f7;
  text-align: center;
  border-radius: 3px;
  &.about-block--secondary {
    background: $secondary;
    color: #222; } }

.our-teams {
  margin-top: 40px;
  padding-bottom: 40px; }

.team {
  position: relative;
  margin: 15px 0;
  .name {
    font-size: 18px;
    line-height: 26px;
    margin-bottom: 5px;
    color: #FFF; }
  .title {
    display: block;
    font-size: 14px;
    line-height: 24px;
    text-transform: uppercase;
    color: #FFF; }
  .socmed {
    color: #FFF;
    padding: 0;
    transform: scaleY(0);
    max-height: 0;
    visibility: hidden;
    opacity: 0;
    transform-origin: bottom center;
    transition: all .5s ease-in-out;
    a {
      color: #FFF;
      display: inline-block;
      padding: 5px 10px;
      border: 1px solid #FFF;
      margin: 0 6px; } }
  &__img {
    width: 100%;
    padding-top: 140%;
    background: #f7f7f7 no-repeat center;
    background-size: cover; }
  &__content {
    width: 100%;
    padding: 15px 10px;
    text-align: center;
    position: absolute;
    bottom: 0;
    left: 0;
    background: rgba(0,0,0,0.7);
    transition: all .5s ease-in-out; }
  &:hover {
    .team__content {
      background: $secondary; }
    .socmed {
      padding: 20px 0 10px;
      transform: scaleY(1);
      max-height: 100px;
      opacity: 1;
      visibility: visible; } } }


.our-work {
  position: relative;
  padding: 60px 0;
  margin-top: 40px;
  background: #f7f7f7 no-repeat center;
  background-size: cover;
  background-attachment: fixed;
  color: #FFF;
  &:after {
    content: "";
    display: block;
    position: absolute;
    z-index: 1;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: #000;
    opacity: .8; }
  .container {
    position: relative;
    z-index: 2; }

  &__title {
    padding-bottom: 40px;
    .title {
      font-size: 3.5rem;
      line-height: 1.15;
      span {
        display: inline-block;
        padding: 12px 20px;
        &+ span {
          margin-left: 18px; } } }
    p {
      max-width: 80%; } } }

.work {
  &__item {
    padding-top: 20px;
    padding-bottom: 20px;
    .icon {
      display: inline-block;
      vertical-align: middle;
      width: 60px;
      line-height: 60px;
      font-size: 18px;
      background: $secondary;
      border-radius: 30px;
      text-align: center; }
    .title {
      color: #FFF;
      text-transform: uppercase;
      font-weight: 400;
      .icon {
        margin-right: 12px; } }
    p {
      padding-right: 30px; } } }



.category {
  &__item {
    border: 1px solid $border;
    border-radius: 3px;
    text-align: center;
    margin-bottom: 48px; }
  &__img {
    position: relative;
    width: 100%;
    display: block;
    .img {
      width: 100%;
      padding-top: 110%;
      background: no-repeat center;
      background-size: cover; } }
  &__content {
    padding: 24px 24px 32px; }
  &__title {
    font-size: 24px;
    line-height: 36px;
    margin-bottom: 10px; }
  &__count {
    display: block;
    margin: 16px; }
  &__text {
    margin-bottom: 24px; } }


.product-single {
  &__img {
    .img {
      width: 100%;
      padding-top: 110%;
      background: no-repeat center;
      background-size: cover; }
    .img-large {
      margin-bottom: 15px; }
    .img-thumb {
      margin: 0 -2px;
      &__item {
        display: inline-block;
        width: 15%;
        border: 2px solid transparent;
        margin-bottom: 2px;
        .img {
          padding-top: 100%; }
        &.active {
          border-color: $primary; } } } }
  &__content {
    padding-left: 30px;
    padding-bottom: 30px; }
  &__title {
    display: block;
    font-size: 48px;
    line-height: 60px;
    font-weight: 500;
    color: $primary;
    margin: 0 0 12px; }
  &__rate {
    margin-bottom: 18px; }
  &__price,
  &__price-range {
    font-size: 28px;
    line-height: 40px;
    color: #222;
    font-weight: 600;
    margin-bottom: 12px;
    small {
      color: #aaa;
      display: block;
      margin: 0;
      font-size: 14px;
      line-height: 20px;
      font-weight: 500;
      text-transform: uppercase; } }
  &__cta {
    padding: 10px 0 24px;
    label {
      margin: 0; }
    .form-group {
      margin-bottom: 24px; } }
  &__description {
    padding: 12px 24px 24px 0;
    dt {
      position: relative;
      padding-right: 10px;
      &:after {
        content: ":";
        display: block;
        position: absolute;
        font-weight: 400;
        top: 0;
        right: 0;
        opacity: .5; } } }
  &__tab {
    margin-bottom: 60px;
    .tab-pane {
      padding: 24px 0; } }

  &__related,
  &__recently {
    padding: 30px 0; } }


.page--auth {
  .forgot {
    padding: 0 0 20px; }
  .form-action {
    p {
      text-align: center;
      padding: 20px 0;
      a {
        color: #007bff;
        &:hover {
          text-decoration: underline; } } } }
  .back-to-shop {
    padding: 30px 0;
    text-align: center;
    .btn-more {
      color: $text;
      &:hover {
        color: $secondary; } } } }


.page-not-found {
  padding: 50px 30px 80px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: calc(100vh - 122px);
  .title {
    margin-bottom: 15px;
    h1 {
      font-size: 144px;
      line-height: 160px;
      font-weight: 300; }
    span {
      font-size: 32px;
      line-height: 48px; } }
  p {
    font-size: 18px;
    line-height: 26px; } }
