// PAGE TRANSITION
#wrap {
  opacity: 0;
  transition: opacity 1.5s ease;
  .header {
    top: -100%;
    opacity: 0;
    transition: all 0.75s ease; }
  main {
    // transform: scale(1.013)
    transition: all 0.75s ease; } }
body {
  overflow-x: hidden;
  transition: all .3s ease-in-out; }
body.load-page {
  #wrap {
    opacity: 1;
    background: #FFF;
    .header {
      top: 0;
      opacity: 1; }
    main {
 } } }      // transform: scale(1)
body.link-transition {
  #wrap {
    opacity: 0;
    .header {
      top: -200%;
      opacity: 0;
      transition: all 2s ease-in-out; }
    main {
      // transform: scale(1.013)
      transition: all 1s ease-in-out; } } }


// animate on scroll
.animate {
  position: relative;
  transition: all .5s ease-in-out;
  &.animate--up {
    top: 100px;
    opacity: 0;
    &.animate--in {
      top: 0;
      opacity: 1; } }
  &.animate--left {
    left: -100px;
    opacity: 0;
    &.animate--in {
      left: 0;
      opacity: 1; } }
  &.animate--delay-1 {
    transition-delay: 0.25s; }
  &.animate--delay-2 {
    transition-delay: 0.5s; }
  &.animate--delay-3 {
    transition-delay: 0.75s; }
  &.animate--delay-4 {
    transition-delay: 1s; }
  &.animate--delay-6 {
    transition-delay: 3s; } }



@mixin bounce() {
  animation-duration: 2s;
  animation-iteration-count: infinite;
  animation-name: bounce;
  animation-timing-function: cubic-bezier(0.280, 0.840, 0.420, 1); }

@keyframes bounce {
  0% {
    transform: scale(1,1) translateY(0); }
  10% {
    transform: scale(1.1,.9) translateY(0); }
  30% {
    transform: scale(.9,1.1) translateY(-16px); }
  50% {
    transform: scale(1.05,.95) translateY(0); }
  57% {
    transform: scale(1,1) translateY(-2px); }
  64% {
    transform: scale(1,1) translateY(0); }
  100% {
    transform: scale(1,1) translateY(0); } }


@keyframes zoom {
  0% {
    transform: scale(1.05); }
  100% {
    transform: scale(1); } }


@keyframes parallaxSlide {
  0% {
    transform: translateX(-10%); }
  100% {
    transform: translateX(0); } }

@keyframes parallaxSlideFrame {
  0% {
    transform: translateX(0); }
  100% {
    transform: translateX(-10%); } }

@keyframes sliderDot {
  0% {
    width: 100%;
    left: 0; }
  85% {
    width: calc(200% + 5px);
    left: 0; }
  100% {
    width: 100%;
    left: calc(100% + 5px); } }

